    export default {
        data() {
            return {
                title: 'request-payment',
                loading: false,
                price: 1,
                providerList: [
					{
						name:'支付类型'
					}
				]
            }
        },
        mounted() {


































        },
        methods: {
            async requestPayment(e, index) {
                this.providerList[index].loading = true;
                let orderInfo = await this.getOrderInfo(e.id);
                console.log("得到订单信息", orderInfo);
                if (orderInfo.msg !== 'success') {
                    console.log("获得订单信息失败", orderInfo);
                    uni.showModal({
                        content: "获得订单信息失败",
                        showCancel: false
                    })
					this.providerList[index].loading = false;
                    return;
                }
				console.log(JSON.stringify(orderInfo.data))
				let isString = typeof(orderInfo.data);
				let oi = isString == "string" ? orderInfo.data : JSON.stringify(orderInfo.data);
                uni.requestPayment({
                    provider: e.id,
					orderInfo: oi,
					success: (e) => {
                        console.log("success", e);
                        uni.showToast({
                            title: "感谢您的赞助!"
                        })
                    },
                    fail: (e) => {
                        console.log("fail", e);
                        uni.showModal({
                            content: "支付失败,原因为: " + e.errMsg,
                            showCancel: false
                        })
                    },
                    complete: () => {
                        this.providerList[index].loading = false;
                    }
                })
            },
            getOrderInfo(payId) {
				if(payId == 'alipay'){
					 return this.$store.dispatch("ALIAPPAY",{
								"orderNo":"120200105000065509",
								"amount":0.5,
								"body":"very good!"
							}).then(res=>{
								return res;
							});
				}
				if(payId == 'wxpay'){
					 return this.$store.dispatch("WXAPPPAY",{
								"orderNo":"120200105000065509",
								"amount":0.5,
								"body":"very good!"
							}).then(res=>{
								return res;
							});
				}else{
					return {
						err:'err'
					}
				}
				
            },
            priceChange(e) {
                console.log(e.detail.value)
                this.price = e.detail.value;
            }
        }
    }
